import React, { useState, useEffect, useRef, useLayoutEffect } from "react";
import ReactDOM from "react-dom";
import { useMutation } from "@apollo/react-hooks";
import { ApolloProvider } from "react-apollo";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import { UserPage } from "./pages/UserPage/UserPage";
import { ListingPage } from "./pages/ListingPage/ListingPage";
import { ListingsPage } from "./pages/ListingsPage/ListingsPage";
import ApolloClient, { InMemoryCache } from "apollo-boost";
import * as serviceWorker from "./serviceWorker";
import { HomeSkeleton } from "./components/Skeletons/HomeSkeleton";
import { Stripe } from "./components/Stripe/Stripe";
import {
  LogInMutationVariables,
  LogInMutation,
} from "./lib/graphql/mutations/__generated__/index.generated";
import { LOG_IN } from "./lib/graphql/mutations/index";
import { Affix, Layout, Spin } from "antd";
import { ErrorBanner } from "./components/Error/Error";
import { LogIn } from "../src/components/Login/LogIn";
import { Viewer } from "./lib/types";
import { AppBar } from "../src/components/AppBar/AppBar";
import { TripsPage } from "./pages/TripsPage/TripsPage";
import { HomePage } from "./pages/HomePage/HomePage";
import { HostPage } from "./pages/HostPage/HostPage";
import { EmptyPage } from "./pages/EmptyPage/EmptyPage";
import { Elements } from "@stripe/react-stripe-js";
import { loadStripe } from "@stripe/stripe-js";

import "./styles/styles.css";
import "./styles/antd.less";

require("dotenv").config();

const client = new ApolloClient({
  uri: "/api",
  request: async (operation) => {
    const token = sessionStorage.getItem("token");
    operation.setContext({
      headers: {
        "X-CSRF-TOKEN": token || "",
      },
    });
  },
  cache: new InMemoryCache(),
});

const initViewer: Viewer = {
  __typename: undefined,
  id: null,
  token: null,
  avatar: null,
  hasWallet: null,
  didRequest: false,
};

const stripePromise = loadStripe(`${process.env.REACT_APP_STRIPE_PUBLISH_KEY}`);

export const ScrollToTop = () => {
  useLayoutEffect(() => {
    window.scrollTo(0, 0);
  }, []);
};

const App = () => {
  const [viewer, setViewer] = useState<Viewer>(initViewer);

  const [logIn, { loading, error }] = useMutation<
    LogInMutation,
    LogInMutationVariables
  >(LOG_IN, {
    onCompleted: (data) => {
      if (data && data.logIn) {
        setViewer(data.logIn);

        if (data.logIn.token) {
          sessionStorage.setItem("token", data.logIn.token);
        } else {
          sessionStorage.removeItem("token");
        }
      }
    },
  });

  const logInRef = useRef(logIn);

  useEffect(() => {
    logInRef.current();
  }, []);

  if (loading) {
    return (
      <Layout className="home-skeleton">
        <HomeSkeleton />
      </Layout>
    );
  }
  return (
    <Elements stripe={stripePromise}>
      <Router>
        {error ? (
          <ErrorBanner description="Log in error, please try again" />
        ) : null}
        <Affix>
          <div>
            <AppBar viewer={viewer} setViewer={setViewer} />
          </div>
        </Affix>

        <Switch>
          <Route exact path="/">
            {!viewer.didRequest && !error ? (
              <Layout className="home-skeleton">
                <div className="home-skeleton-spinner">
                  <Spin size="large" tip="Launching Home Mobile" />
                </div>
              </Layout>
            ) : null}

            <HomePage />
          </Route>

          <Route exact path="/listings/:location?">
            <ListingsPage />
          </Route>

          <Route exact path="/listing/:id">
            <ListingPage viewer={viewer} />
          </Route>

          <Route exact path="/host">
            <HostPage viewer={viewer} />
          </Route>

          <Route exact path="/login">
            <LogIn setViewer={setViewer} />
          </Route>

          <Route exact path="/stripe">
            <Stripe viewer={viewer} setViewer={setViewer} />
          </Route>

          <Route exact path="/user/:id">
            <UserPage viewer={viewer} setViewer={setViewer} />
          </Route>

          <Route exact path="/trips/:id">
            <TripsPage viewer={viewer} setViewer={setViewer} />
          </Route>

          <Route component={EmptyPage} />
        </Switch>
      </Router>
    </Elements>
  );
};

ReactDOM.render(
  <ApolloProvider client={client}>
    <App />
  </ApolloProvider>,

  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
